  { path: 'users/new', loadChildren: './pages/new-user-page/new-user-page.module#NewUserPageModule', canActivate: [AuthGuard] },
  { path: 'users/:id', loadChildren: './pages/edit-user-page/edit-user-page.module#EditUserPageModule', canActivate: [AuthGuard] }
